.assortment {
    position: relative;

    margin-bottom: 17.9rem;

    &__inner {
        display: flex;
    }

    &__left-block {
        display: flex;
        flex-direction: column;
    }

    &__subtitle {
        font-size: 1.8rem;
        font-weight: 400;
        line-height: 1.2;
        text-transform: uppercase;
        color: #777777;

        margin-bottom: 1.5rem;
    }

    &__title {
        font-size: 4.5rem;
        font-weight: 700;
        line-height: 1.2;

        width: 53.6rem;

        margin-bottom: 4.3rem;
    }

    &__par {
        font-size: 1.95rem;
        font-weight: 300;
        line-height: 1.4;

        width: 69.9rem;

        margin-bottom: 4rem;
    }

    &__photo-wrapper {
        display: flex;
    }

    &__img-big {
        width: 50.2rem;
        height: 43.4em;
    }

    &__img-block {
        width: 43rem;
        height: 43rem;
    }

    &__img-block-top {
        display: flex;
        flex-direction: row-reverse;
    }

    &__img-mini-top {
        width: 21.5rem;
        height: 21.6rem;
    }

    &__img-block-down {
        display: flex;
    }
    
    &__img-mini-down {
        width: 21.5rem;
        height: 22rem;
    }

    &__left-block-down {
        display: flex;
    }

    &__left-down-text {
        width: 50.2rem;

        padding-top: 4.0rem;
    }

    &__left-down-par {
        font-size: 1.95rem;
        font-weight: 300;
        line-height: 1.4;

        width: 45.2rem;

        margin-bottom: 7.9rem;
    }

    &__left-block-link {
        margin-left: 10rem;

        &::before {
            transform: translate(-40%, -10%);
        }
    }

    &__left-down-img-wrap {
        width: 43rem;
        
        display: flex;
        flex-direction: row-reverse;

        background-color: #ECECEC;
    }

    &__left-down-img {
        width: 21.5rem;
        height: 21.6rem;
    }


    &__right-block {
        padding-top: 8.9rem;
    }


    &__slider-img {
        width: 56.8rem;
        height: 43.4rem;

        position: relative;
        z-index: 1;
    }

    &__right-block-img-wrap {
        width: 43.2rem;
        height: 43.2rem;
    }

    &__right-block-img-top {
        display: flex;
    }

    &__right-block-img {
        width: 21.5rem;
        height: 22rem;
    }

    &__right-block-img-down {
        display: flex;
        width: 43.2rem;
        height: 21.4rem;
        background-color: #ECECEC;
    }

    &__img-right {
        width: 55.2rem;
        height: 21.4rem;

        position: absolute;
        bottom: 0;
        right: 0;
    }

    &__white-block {
        width: 20.9rem;
        height: 21.5rem;

        background-color: #ffffff;

        position: absolute;
        top: 9rem;
        right: 0;
    }
}